.line{
    display: flex;
    width: 96%;
    margin: 1% 2%;
    justify-content: space-between;
    font-size: 4vw;
}

.line__name {
    width: 12%;
    font-family: 'Bold Dot Digital-7';
}

.line__direction {
    width: 70%;
}

.line__time {
    width: 18%;
    text-align: right;
    font-family: 'Bold Dot Digital-7';
}